.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.nav
{
  width: 100%;
  height: 100px;
  background-color: #61dafb;
}
.my
{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: center;
  font-size: 50px;
  margin-top: 50px;
}
.ft
{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: center;
  font-size: 40px;
  margin-top: 50px;
  color: #61dafb;
}
.futer
{
  width: 100%;
  height: 100px;
  background-color: #000000;
  float: left;
}
.left
{
  height: 450px;
  width: 30%;
  background-color: pink;
  float: left;
}
.center
{
  height: 450px;
  width: 40%;
  background-color: rgb(98, 0, 17);
  float: left;
  
}
.right
{
  height: 450px;
  width: 30%;
