}

.menu
{
  overflow: hidden;
  background-color: rgb(1, 35, 49);
  position: fixed;
  width: 100%;
}
.menu a {
  float: left;
  margin-left: 10px;
   
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
.menu a:hover {
  background: #f1f1f1;
  color: black;
}